<template>
  <div id="register">
    <el-row>
      <img src="../assets/logo_login.png">
      <h3 style="color: #666666"></h3>
    </el-row>
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-position="left" label-width="0px" class="login-container">
      <el-form-item prop="userName">
        <el-input v-model="ruleForm.userName"  type="text"  auto-complete="off" placeholder="+20"></el-input>    <!--v-model-->
      </el-form-item>
      <el-form-item  prop="password" >
        <el-input v-model="ruleForm.password"  type="password"  auto-complete="off" placeholder="620"></el-input>
      </el-form-item>
      <el-form-item prop="phone">
        <el-input v-model.number="ruleForm.phone"  placeholder=""></el-input>    <!--v-model-->
      </el-form-item>
      <el-form-item prop="code">
        <el-row>
          <el-col :span="14"><el-input v-model.number="ruleForm.code"  placeholder=""></el-input></el-col>
          <el-col :span="8"><el-button type="primary" class="nextButton codeButton"></el-button></el-col>
        </el-row>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" class="nextButton" id="nextBtn" @click="nextStep('ruleForm')"></el-button>
      </el-form-item>
    </el-form>
    <el-row style="text-align: center">
      <span class="registerStep highlight">1  </span><span class="registerStep">2  </span><span class="registerStep">3 </span>
    </el-row>
  </div>
</template>

<script>
  import md5 from 'js-md5';

  export default {
    name: 'register',
    data(){
      //
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if(value.length < 6 || value.length > 20){
          callback(new Error('6-20'));
        }else {
          callback();
        }
      };
      var validateUserName = (rule, value, callback) => {
        var regp =/^(?=.*[a-zA-Z]+)(?=.*[0-9]+)[a-zA-Z0-9]+$/;
        if(value === ''){
          callback(new Error(''));
        }else if (!regp.test(value) || value.length>20){
          callback(new Error('+20'));
        }else {
          callback();
        }
      };
      var validatePhone = (rule, value, callback) => {
        if (!(/^1[34578]\d{9}$/.test(value))){
          callback(new Error(''));
        }else {
          callback();
        }
      };
      return{
        ruleForm: {
          userName: '',
          password: '',
          phone:'',
          code:''
        },
        rules: {
          userName: [
            { validator: validateUserName, trigger: 'blur' },   //
          ],
          password: [
            { validator: validatePass, trigger: 'blur' }
          ],
          phone: [
            { required: true, message: ''},
            { type: 'number', message: ''},
            { validator: validatePhone, trigger: 'blur' }
          ]
        },
      }
    },
    methods:{
      nextStep(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
          this.$router.push('/registerDetail/'+ this.ruleForm.userName +'/pwd/'+ md5(this.ruleForm.password)+'/phone/' +  this.ruleForm.phone);
          } else {
            return false;
          }
        });
      }
    }
  }
</script>



