<template>
  <div id="register">
    <el-row>
      <img src="../assets/logo_login.png">
      <h3 style="color: #666666"></h3>
    </el-row>
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-position="left" label-width="0px" class="login-container" v-loading="loading" element-loading-text="...">
      <el-form-item prop="enterpriseName">
        <el-input v-model="ruleForm.enterpriseName"  type="text"  auto-complete="off" placeholder=""></el-input>    <!--v-model-->
      </el-form-item>
      <el-form-item prop="acctSvcrName" >
        <el-input v-model="ruleForm.acctSvcrName"  type="text"  auto-complete="off" placeholder=""></el-input>
      </el-form-item>
      <el-form-item prop="acctIds">
        <el-input v-model.number="ruleForm.acctIds" placeholder=""></el-input>    <!--v-model-->
      </el-form-item>
      <el-form-item prop="roleCode" style="text-align: left">
        <el-radio-group v-model="ruleForm.roleCode" size="small">
          <el-radio-button name="roleCode" label="0"></el-radio-button>
          <el-radio-button name="roleCode" label="2"></el-radio-button>
          <el-radio-button name="roleCode" label="1"></el-radio-button>
          <el-radio-button name="roleCode" label="3"></el-radio-button>
        </el-radio-group>
      </el-form-item>
      <el-row class="agreeMsg"><span class="tip"><span style="color:rgb(57,202,166);">xxx</span></span></el-row>
      <el-form-item style="width:100%;">
        <el-button type="primary" class="nextButton" id="registerBtn" @click="register('ruleForm')"></el-button>
      </el-form-item>
    </el-form>
    <el-row style="text-align: center">
      <span class="registerStep highlight">1  </span><span class="registerStep highlight">2  </span><span class="registerStep">3 </span>
    </el-row>
  </div>
</template>

<script>
  import Store from "../../../common/store.js"

  export default {
    name: 'register',
    data(){
      return{
        dialogVisible:false,
        msg:'',
        loading:false,
        ruleForm: {
          accountName: '',     //
          password:'',         //
          enterpriseName:'',   //
          phone:'111',         //
          roleCode: "0",         //code
          securityCode:'1234', //
          securityCodeId:1,    //id
          certType:"id",       //
          certNo:'33028',      //
          acctIds:"",      //
          svcrClass:'316',     //
          acctSvcrName:'',     //
          acctSvcr:'1',         //
        },
        rules: {
          account: [
            { required: true, message: '', trigger: 'blur' },   //
            { type: 'number', message: ''},
          ],
          companyName: [
            { required: true, message: '', trigger: 'blur' },
          ],
          bank: [
            { required: true, message: '', trigger: 'blur' },
          ]
        },
      }
    },
    methods:{
      register(formName) {
        this.ruleForm.accountName = this.$route.params.userName;
        this.ruleForm.password = this.$route.params.pwd;
        this.ruleForm.phone = this.$route.params.phone;

        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.loading = true;
            this.$http.post('../v1/account/user',this.ruleForm,{emulateJSON:true}).then((res) => {
              this.loading = false;
              var data =  res.body.data;
              if(res.body.code != 0){
                this.$message.error(res.body.message);
                return;
              }
              Store.saveUserInfo(data);
              this.$router.push('/registerSuccess')
            },(err) => {
              this.loading = false;
              console.log(err);
            })
          } else {
            return false;
          }
        });
      }
    }
  }
</script>



